import React from "react";
import '../styles/table-style.css';

import first from '../img/first.jpg'
import second from '../img/second.jpg'
import third from '../img/third.jpg'

class Tabled extends React.Component {
    render() {
        return(
            <table>
                <td className='td'>
                    <img className='brainFight' src={third} />
                    <div className='title'><a className='eventlink' target='_blank' href='https://loozmax.github.io/event-urfu/#/api/events/2'>Мозгобитва</a></div>
                </td>
                <td className='td'>
                    <img className='brainFight' src={second} />
                    <div className='title'><a className='eventlink' target='_blank' href='https://loozmax.github.io/event-urfu/#/api/events/5'>Встреча языкового клуба</a></div>
                </td>
                <td className='td'>
                    <img className='brainFight' src={first} />
                    <div className='title'><a className='eventlink' target='_blank' href='https://loozmax.github.io/event-urfu/#/api/events/8'>Запуск сайта УрФУ.События!</a></div>
                </td>
            </table>
        )
    }
}

export default Tabled;